import React, { useState, useEffect } from "react";
import Navigation from "../components/Navigation";
import Header from "../components/UI/Header";
import styles from "./styles.module.css";
import { Link, Route } from "react-router-dom";
import { Box, Image, Center } from "@chakra-ui/react";
import { useSelector, useDispatch } from "react-redux";
import { getTeam } from "../redux/actions/adminActions";

const Team = () => {
  const dispatch = useDispatch();
  const managerTeam = useSelector((state) => state.managerTeam);
  const { loading, error, team } = managerTeam;
  useEffect(() => {
    dispatch(getTeam());
  }, [dispatch]);
  return (
    <div>
      <Route render={({ history }) => <Navigation history={history} />} />
      <div className={styles.mainContents}>
        <div className={styles.overlay}>
          <Header title="My Team" />
        </div>
        <div className={styles.contentSm}>
          <div className={styles.appraisal}>
            {team &&
              team.map((item) => (
                <div key={item._id}>
                  <Box
                    maxW="sm"
                    borderWidth="1px"
                    borderRadius="lg"
                    overflow="hidden"
                  >
                    <Center>
                      <Image
                        src={`/uploads/${item ? item.photo : ""}`}
                        alt={item.photo}
                      />
                    </Center>

                    <Box d="flex" alignItems="baseline">
                      <Box
                        color="gray.500"
                        fontWeight="semibold"
                        letterSpacing="wide"
                        fontSize="md"
                        textTransform="uppercase"
                      >
                        {item.firstname + " " + item.lastname}
                      </Box>
                    </Box>
                    <Box d="flex" alignItems="baseline">
                      <Box color="gray.500" fontWeight="semibold" fontSize="xs">
                        {item.department}
                      </Box>
                    </Box>
                  </Box>
                </div>
              ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Team;
