import axios from "axios";
import {
  GET_INV_SUCCESS,
  GET_INV_REQUEST,
  GET_INV_FAIL,
  GET_CURRENT_SUCCESS,
  GET_CURRENT_REQUEST,
  GET_CURRENT_FAIL,
} from "../constants/invConstants";

export const getInventory = (year) => async (dispatch, getState) => {
  try {
    dispatch({ type: GET_INV_REQUEST });

    const {
      userLogin: { userInfo },
    } = getState();

    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };
    const { data } = await axios.get(
      `/api/v1/staff/score/appraisalscore/${year}`,
      config
    );

    dispatch({
      type: GET_INV_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: GET_INV_FAIL,
      payload:
        error.response && error.response.data.error
          ? error.response.data.error
          : error.message,
    });
  }
};

