import {
  GET_INV_FAIL,
  GET_INV_REQUEST,
  GET_INV_SUCCESS,
  GET_CURRENT_FAIL,
  GET_CURRENT_REQUEST,
  GET_CURRENT_SUCCESS,
} from "../constants/invConstants";

export const getInvReducer = (state = {}, action) => {
  switch (action.type) {
    case GET_INV_REQUEST:
      return { ...state, loading: true };
    case GET_INV_SUCCESS:
      return {
        loading: false,
        success: true,
        first: action.payload.firstQuarter,
        second: action.payload.secondQuarter,
        third: action.payload.thirdQuarter,
        fourth: action.payload.fourthQuarter,
      };
    case GET_INV_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};

export const getCurrentReducer = (
  state = { score: 0, manager: 0, hr: 0, overall: 0 },
  action
) => {
  switch (action.type) {
    case GET_CURRENT_REQUEST:
      return { ...state, loading: true };
    case GET_CURRENT_SUCCESS:
      return {
        loading: false,
        success: true,
        score: action.payload.score,
        manager: action.payload.manager,
        hr: action.payload.hr,
        overall: action.payload.overall,
      };
    case GET_CURRENT_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};
