import {
  GET_ALL_STAFF_REQUEST,
  GET_ALL_STAFF_SUCCESS,
  GET_ALL_STAFF_FAIL,
  MYTEAM_FAIL,
  MYTEAM_REQUEST,
  MYTEAM_SUCCESS,
} from "../constants/adminConstants";

export const getAllStaffReducer = (state = { staffs: [] }, action) => {
  switch (action.type) {
    case GET_ALL_STAFF_REQUEST:
      return { ...state, loading: true };
    case GET_ALL_STAFF_SUCCESS:
      return { loading: false, success: true, staffs: action.payload.data };
    case GET_ALL_STAFF_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};

export const getMyTeamReducer = (state = { team: [] }, action) => {
  switch (action.type) {
    case MYTEAM_REQUEST:
      return { ...state, loading: true };
    case MYTEAM_SUCCESS:
      return { loading: false, success: true, team: action.payload.data };
    case MYTEAM_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};
