import React from 'react'
import ReactECharts from "echarts-for-react";

const Chart = ({ first, second, third, fourth }) => {
    const a = first && first
    const b = second && second
    const c = third && third
    const d = fourth && fourth
    const e = a + b + c + d
    const overall = e / 4

    const option = {
        tooltip: {
            trigger: "item",
        },

        legend: {
            top: "5%",
            left: "center",
        },
        series: [
            {
                name: "KPI",
                type: "pie",
                radius: ["40%", "70%"],
                avoidLabelOverlap: false,
                itemStyle: {
                    borderRadius: 10,
                    borderColor: "#fff",
                    borderWidth: 2,

                },
                label: {
                    show: false,
                    position: "center",
                },
                emphasis: {

                    label: {
                        show: true,
                        fontSize: "20",
                        fontWeight: "bold",
                    },
                },
                labelLine: {
                    show: false,
                },
                data: [
                    { value: first ? first : 0, name: "First Quarter" },
                    { value: second ? second : 0, name: "Second Quarter" },
                    { value: third ? third : 0, name: "Third Quarter" },
                    { value: fourth ? fourth : 0, name: "Four Quarter" },
                    { value: overall ? overall : 0, name: "Overall" },

                ],
            },
        ],
    };
    return (
        <div>
            <ReactECharts option={option} style={{ height: '500px', width: '100%' }} />
        </div>
    )
}

export default Chart
